import React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import yii from '../img/yii2.png';
import css from '../img/css-3.png';
import js from '../img/js.png';
import tauri from '../img/tauri.png';

import CardCarousel from './CardCarousel';
import { Row,Col } from "react-bootstrap";


export default function MediaCard() {
  return (
    <Card sx={{ maxWidth: 345 }}>
    <CardCarousel/>
      <CardContent>
        <Row>
            <Col>
        <Typography gutterBottom variant="h5" component="div" sx={{fontSize:'18px'}}>
          GAIA Platform
        </Typography>
        </Col>
        <Col>
        <div style={{float:'right'}}>
           <img src={yii} style={{width:'30px',padding:'5px'}}></img>
           <img src={tauri} style={{width:'30px',padding:'5px'}}></img>
           <img src={js} style={{width:'30px',padding:'5px'}}></img>
        </div>
        </Col>
        </Row>
        <Typography variant="body2" color="text.secondary">
          Lizards are a widespread group of squamate reptiles, with over 6,000
          species, ranging across all continents except Antarctica
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Go to website</Button>
      </CardActions>
    </Card>
  );
}
